.navbar {
    display: flex;
    justify-content: space-between;
    background-color: var(--color2);
    padding: 1em;
  }
  
  .list {
    display: flex;
    list-style: none;
    align-items: center;
  }
  
  .item {
    margin-right: 1em;
  }
  
  .item a {
    color: #fff;
    text-decoration: none;
  }
  
  .item a:hover {
    color: var(--color1);
    text-decoration: none;
  }